<template>
  <div class="message-box">
    <el-row class="messageboxheader"></el-row>
    <el-table :data="messageList">
      <!-- :data:  -->
      <!-- border  -->
      <!-- type="index"  -->
      >
      <el-table-column type="index"> </el-table-column>
      <el-table-column prop="baseName" label=""></el-table-column>
      <el-table-column prop="baseAddr" label=""></el-table-column>
      <el-table-column prop="createBy" label=""></el-table-column>
      <el-table-column prop="scope" label=""></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <!--  -->
          <el-button
            type="primary"
            icon="el-icon-check"
            size="small"
            @click="invitationEnter(scope.row.id, 1)"
          ></el-button>
          <!--  -->
          <!-- type="danger":  -->
          <el-button
            type="danger"
            icon="el-icon-close"
            size="small"
            @click="invitationEnter(scope.row.id, 0)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pageInfo.pagenum"
      :page-sizes="[4, 6, 8, 10]"
      :page-size="pageInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>
  </div>
</template>

<script>
import { domain } from "../../../router/url";
export default {
  data() {
    return {
      token: "Bearer " + window.sessionStorage.getItem("token"),
      messageList: [],
      // 
      pageInfo: {
        // 
        pagenum: 1,
        // 
        pagesize: 4,
      },

      // 
      total: 0,
    };
  },
  methods: {
    async getMessageList() {
      const { data: res } = await this.$http.get(
        `${domain.Base_M9_URL}/user/invitation/1/4`,
        {
          headers: {
            Authorization: this.token,
          },
        }
      );
      this.total = res.data.total;
      this.messageList = res.data.rows;
      console.log(res);
    },
    // pagesize()
    handleSizeChange(newSize) {
      this.pageInfo.pagesize = newSize;
      this.getMessageList();
    },

    // 
    handleCurrentChange(newPage) {
      this.pageInfo.pagenum = newPage;
      this.getMessageList();
    },
    // 
    async invitationEnter(baseId, selection) {
      const { data: res } = await this.$http.put(
        `${domain.Base_M9_URL}/user/invitation/${baseId}/${selection}`,
        {},
        {
          headers: {
            Authorization: this.token,
          },
        }
      );
      console.log(res);
      if (res.message == "") {
        if (selection == 1) {
          this.$router.push('/basePage')
          return this.$message.success('')
        } else if (selection == 0) {
          this.getMessageList();
          return this.$message.success('')
        }
      }
    },
  },
  created() {
    this.getMessageList();
  },
};
</script>

<style lang="less" scoped>
.message-box {
  background-color: #fff;
  .messageboxheader {
    font-size: 20px;
    font-weight: bold;
    color: #5a5858;
    padding: 10px;
  }
}
</style>