<template>
  <div class="articleManage">
    <div class="title">
      <h2></h2>
      <div class="addArticle">
        <el-button
          type="primary"
          size="medium"
          @click="handleAdd"
          icon="el-icon-plus"
          ></el-button
        >
      </div>
    </div>
    <el-tabs
      type="border-card"
      v-loading="loading"
      v-model="tabIndex"
      @tab-click="handleClick"
    >
      <el-tab-pane label="">
        <el-table :data="articleList" style="width: 100%" max-height="500">
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-form label-position="left" class="demo-table-expand">
                <el-form-item label="id">
                  <span>: {{ props.row.id }}</span>
                </el-form-item>
                <el-form-item label="id">
                  <span>: {{ props.row.typeId }}</span>
                </el-form-item>
                <el-form-item label="">
                  <span>: {{ props.row.title }}</span>
                </el-form-item>
                <el-form-item label="">
                  <span>: {{ props.row.summary }}</span>
                </el-form-item>
                <el-form-item label="">
                  <div v-html="props.row.content"></div>
                </el-form-item>
                <el-form-item label="">
                  <span>: {{ props.row.creationTime }}</span>
                </el-form-item>
                <el-form-item label="">
                  <span>: {{ props.row.stateInfo }}</span>
                </el-form-item>
                <el-form-item label="">
                  <span>: {{ props.row.clickNum }}</span>
                </el-form-item>
                <el-form-item label="">
                  <span>: {{ props.row.recommend }}</span>
                </el-form-item>
                <el-form-item label=""
                  >:
                  <img :src="props.row.picture" alt="" />
                </el-form-item>
                <el-form-item label="">
                  <span
                    >:
                    {{
                      props.row.editor == "" ? "" : props.row.editor
                    }}</span
                  >
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <el-table-column fixed prop="creationTime" label="" width="180">
          </el-table-column>
          <el-table-column prop="title" label="" width="400">
          </el-table-column>
          <el-table-column prop="stateInfo" label="" width="100">
          </el-table-column>
          <el-table-column fixed="right" label="" width="120">
            <template slot-scope="scope">
              <el-button
                @click.native.prevent="deleteRow(scope.$index, articleList)"
                type="text"
                size="small"
              >
                
              </el-button>
              <el-button
                @click="handleEdit(scope.$index, scope.row)"
                type="text"
                size="small"
                ></el-button
              >
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
      <el-tab-pane label="">
        <el-table
          :data="deleteArticleList"
          style="width: 100%"
          max-height="500"
        >
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-form label-position="left" class="demo-table-expand">
                <el-form-item label="id">
                  <span>: {{ props.row.id }}</span>
                </el-form-item>
                <el-form-item label="id">
                  <span>: {{ props.row.typeId }}</span>
                </el-form-item>
                <el-form-item label="">
                  <span>: {{ props.row.title }}</span>
                </el-form-item>
                <el-form-item label="">
                  <span>: {{ props.row.summary }}</span>
                </el-form-item>
                <el-form-item label="">
                  <div v-html="props.row.content"></div>
                </el-form-item>
                <el-form-item label="">
                  <span>: {{ props.row.creationTime }}</span>
                </el-form-item>
                <el-form-item label="">
                  <span>: {{ props.row.stateInfo }}</span>
                </el-form-item>
                <el-form-item label="">
                  <span>: {{ props.row.clickNum }}</span>
                </el-form-item>
                <el-form-item label="">
                  <span>: {{ props.row.recommend }}</span>
                </el-form-item>
                <el-form-item label=""
                  >:
                  <img :src="props.row.picture" alt="" />
                </el-form-item>
                <el-form-item label="">
                  <span
                    >:
                    {{
                      props.row.editor == "" ? "" : props.row.editor
                    }}</span
                  >
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <el-table-column fixed prop="creationTime" label="" width="180">
          </el-table-column>
          <el-table-column prop="title" label="" width="520">
          </el-table-column>
          <el-table-column prop="stateInfo" label="" width="100">
          </el-table-column>
        </el-table>
      </el-tab-pane>
    </el-tabs>
    <!-- -- -->
    <el-dialog title="" :visible.sync="dialogFormVisibleUpdate">
      <el-row>
        <el-col :span="18">
          <el-form
            :model="ruleForm"
            :rules="rules"
            ref="ruleForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item :label="label.id" prop="id">
              <el-input
                v-model.number="ruleForm.id"
                placeholder=""
                disabled
              ></el-input>
            </el-form-item>
            <el-form-item :label="label.title" prop="title">
              <el-input
                type="textarea"
                autosize
                v-model="ruleForm.title"
                placeholder=""
              ></el-input>
            </el-form-item>
            <el-form-item :label="label.editor" prop="editor">
              <el-input
                v-model="ruleForm.editor"
                placeholder=""
              ></el-input>
            </el-form-item>
            <el-form-item :label="label.summary" prop="summary">
              <el-input
                type="textarea"
                autosize
                v-model="ruleForm.summary"
                placeholder=""
              ></el-input>
            </el-form-item>
            <el-form-item
              :label="label.creationTime"
              prop="creationTime"
              style="display: none"
            >
              <el-input
                v-model="ruleForm.creationTime"
                placeholder=""
              ></el-input>
            </el-form-item>
            <el-form-item :label="label.typeId" prop="typeId">
              <el-select v-model="ruleForm.typeId" placeholder="">
                <el-option
                  v-for="item in options"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item :label="label.content" prop="content">
              <mq-editor
                v-model="ruleForm.content"
                upload-url="/information/upload"
                fileName="file"
              />
            </el-form-item>
            <el-form-item label="">
              <el-upload
                :span="2"
                class="upload-demo"
                action="http://106.75.154.40:9005/information/upload"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                :on-success="handleSuccess"
                :file-list="fileList"
                list-type="picture"
              >
                <el-button
                  size="small"
                  type="primary"
                  v-if="!fileList[0]"
                  class="btnTop"
                  ></el-button
                >
                <div slot="tip" class="el-upload__tip">
                  jpg/png500kb
                </div>
              </el-upload>
            </el-form-item>
          </el-form>
          <div class="form-button">
            <el-button type="success" @click="submitForm('ruleForm')"
              ></el-button
            >
          </div>
        </el-col>
      </el-row>
    </el-dialog>
    <!-- -- -->
    <!-- -- -->
    <el-dialog title="" :visible.sync="dialogFormVisibleAdd">
      <el-row>
        <el-col :span="18">
          <el-form
            :model="ruleForm"
            :rules="rules"
            ref="ruleForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <!-- <el-form-item :label="label.id" prop="id">
              <el-input
                v-model.number="ruleForm.id"
                placeholder=""
                disabled
              ></el-input>
            </el-form-item> -->
            <el-form-item :label="label.title" prop="title">
              <el-input
                type="textarea"
                autosize
                v-model="ruleForm.title"
                placeholder=""
              ></el-input>
            </el-form-item>
            <el-form-item :label="label.editor" prop="editor">
              <el-input
                v-model="ruleForm.editor"
                placeholder=""
              ></el-input>
            </el-form-item>
            <el-form-item :label="label.summary" prop="summary">
              <el-input
                type="textarea"
                autosize
                v-model="ruleForm.summary"
                placeholder=""
              ></el-input>
            </el-form-item>
            <el-form-item
              :label="label.creationTime"
              prop="creationTime"
              style="display: none"
            >
              <el-input
                v-model="ruleForm.creationTime"
                placeholder=""
              ></el-input>
            </el-form-item>
            <el-form-item :label="label.typeId" prop="typeId">
              <el-select v-model="ruleForm.typeId" placeholder="">
                <el-option
                  v-for="item in options"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item :label="label.content" prop="content">
              <mq-editor
                v-model="ruleForm.content"
                upload-url="/information/upload"
                fileName="file"
              />
            </el-form-item>
            <el-form-item label="">
              <el-upload
                :span="2"
                class="upload-demo"
                action="http://106.75.154.40:9005/information/upload"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                :on-success="handleSuccess"
                :file-list="fileList"
                list-type="picture"
              >
                <el-button
                  size="small"
                  type="primary"
                  v-if="!fileList[0]"
                  class="btnTop"
                  ></el-button
                >
                <div slot="tip" class="el-upload__tip">
                  jpg/png500kb
                </div>
              </el-upload>
            </el-form-item>
          </el-form>
          <div class="form-button" style="text-align: center">
            <el-button type="success" @click="submitForm('ruleForm')"
              ></el-button
            >
          </div>
        </el-col>
      </el-row>
    </el-dialog>
    <!-- -- -->
    <pagination
      :total="total"
      :size="size"
      :resetPage="resetPage"
      @pageChange="handlePageChange"
    ></pagination>
  </div>
</template>
<script>
import pagination from "../expertListChildren/pagination";
import mqEditor from "./expertManageChildren/mq-editor";
export default {
  data() {
    return {
      // 
      articleList: [],
      expertId: "",
      total: 50,
      page: 1,
      size: 5,
      // 
      resetPage: false,
      // 
      oneArticle: {},
      // 
      label: {
        id: "id",
        title: "",
        editor: "",
        summary: "",
        creationTime: "",
        typeId: "id",
        content: "",
      },
      // 
      fileList: [],
      // 
      options: [],
      // 
      dialogFormVisibleUpdate: false,
      dialogFormVisibleAdd: false,
      // 
      ruleForm: {},
      // 
      isAddBtn: false,
      // 
      rules: {
        title: [{ required: true, message: "", trigger: "blur" }],
        editor: [{ required: true, message: "", trigger: "blur" }],
        typeId: [
          {
            required: true,
            message: "id",
            trigger: "change",
          },
        ],
        content: [{ required: true, message: "", trigger: "change" }],
      },
      deleteArticleList: [],
      loading: true,
      tabIndex: 0,
    };
  },
  components: {
    pagination,
    mqEditor,
  },
  methods: {
    // tab
    handleClick() {
      console.log(this.tabIndex);
      if (this.tabIndex == 0) {
        // expertId
        this.getArticleListByExpertId(this.expertId);
      } else if (this.tabIndex == 1) {
        this.getDeletedArticleList();
      }
      this.resetPage = true;
    },
    // 
    getDeletedArticleList(page = 1, size = 5) {
      this.reqM2Service(
        `/info/diseaseArticles/findDelete/${page}/${size}`,
        {},
        "get"
      ).then((res) => {
        res = res.data;
        if (res.code == 20000) {
          res = res.data;
          this.total = res.total;
          this.deleteArticleList = [];
          console.log("res: ", res);
          res.rows.forEach((item) => {
            // state
            item.stateInfo = "";
            // 
            item.creationTime = this.formatTime(item.creationTime);
            this.deleteArticleList.push(item);
          });
        } else {
          this.$message({
            message: res.message,
          });
        }
        this.loading = false;
      });
    },
    // 
    handleAdd() {
      this.isAddBtn = true;
      this.dialogFormVisibleAdd = true;
      this.getTypes();
    },
    // 
    async handleEdit(index, row) {
      this.dialogFormVisibleUpdate = true;
      let id = row.id;
      // console.log('id: ', id);
      // 
      await this.getArticleById(id);
      console.log("this.oneArticle: ", this.oneArticle);
      this.ruleForm = this.oneArticle;
      // 
      await this.getTypes();
      // console.log('this.options: ', this.options);
      this.fileList = [{ url: this.oneArticle.picture }];
    },
    // id
    async getArticleById(id) {
      await this.reqM2Service(`/info/information/${id}`, {}, "get").then(
        (res) => {
          res = res.data;
          if (res.code == 20000) {
            console.log("res: ", res);
            res = res.data;
            this.oneArticle = res;
          } else {
            this.$message({
              message: "",
            });
          }
        }
      );
    },
    // 
    async getTypes() {
      await this.reqM2Service(`/info/diseaseArticlesTypes`, {}, "get")
        .then((res) => {
          res = res.data;
          // console.log('res: ', res);
          if (res.code === 20000) {
            res = res.data;
            return res;
          } else {
            this.$message({
              message: "",
            });
          }
        })
        .then((options) => {
          this.options = options;
          if (!this.isAddBtn) {
            // typeid
            this.options.some((item) => {
              // console.log('item: ', item.id,'this.ruleForm.typeId: ', this.ruleForm.typeId);
              if (item.id == this.oneArticle.typeId) {
                this.ruleForm.typeId = item.name;
                return true;
              }
              return false;
            });
          }
        });
    },
    // 
    deleteRow(index, rows) {
      // console.log('rows: ', rows);
      // console.log('index: ', index);
      let id = rows[index].id;
      console.log("id: ", id);
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.reqM2Service(`/info/diseaseArticles/delete/${id}`, {}, "delete")
            // this.$http
            //   .delete(
            //     `http://106.75.154.40:9012/info/diseaseArticles/delete/${id}`
            //   )
            .then((res) => {
              res = res.data;
              console.log("res: ", res);
              if (res.code == 20000) {
                this.$message({
                  message: res.message,
                });
                // 
                this.getArticleListByExpertId(
                  this.expertId,
                  this.page,
                  this.size
                );
              } else {
                this.$message({
                  message: "",
                });
              }
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    // 
    handlePageChange({ page, size }) {
      // console.log("size: ", size);
      // console.log("page: ", page);
      if (this.tabIndex == 0) {
        this.getArticleListByExpertId(this.expertId, page, size);
      } else if (this.tabIndex == 1) {
        this.getDeletedArticleList(page, size);
      }
      // 
      this.resetPage = false;
    },
    // 
    submitForm(formName) {
      // 
      if (!this.fileList[0].url) {
        this.$message({
          message: "",
        });
        return;
      }
      this.ruleForm.picture = this.fileList[0].url;
      console.log("this.ruleForm: ", this.ruleForm);
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.isAddBtn) {
            this.addArticle(this.ruleForm, this.expertId);
          } else {
            this.updateArticle(this.ruleForm);
          }
        } else {
          this.$message({
            message: "!",
          });
          return false;
        }
      });
    },
    // 
    addArticle(oneArticle, expertId) {
      this.reqM2Service(
        `/info/diseaseArticles/add?expertsId=${expertId}`,
        oneArticle,
        "post"
      ).then((res) => {
        res = res.data;
        if (res.code == 20000) {
          this.dialogFormVisibleAdd = false;
          this.$message({
            type:"success",
            message: res.message,
          });
          // this.resetForm("ruleForm");
          // this.fileList=[];
        } else {
          this.$message({
            message: "!",
          });
        }
      });
    },
    // 
    updateArticle(oneArticle) {
      // this.$http
      //   .put(
      //     `http://106.75.154.40:9005/diseaseArticles/update/${oneArticle.id}`,
      //     oneArticle
      //   )
      this.reqM8Service(
        `/diseaseArticles/update/${oneArticle.id}`,
        oneArticle,
        "put"
      ).then((res) => {
        res = res.data;
        if (res.code == 20000) {
          // console.log('res: ', res);
          this.dialogFormVisibleUpdate = false;
          // 
          this.getArticleListByExpertId(this.expertId, this.page, this.size);
          this.$message({
            message: res.message,
          });
        } else {
          this.$message({
            message: "!",
          });
        }
      });
    },
    // 
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    // 
    handleRemove(file, fileList) {
      this.fileList = fileList;
    },
    // 
    handlePreview(file) {
      console.log(file);
    },
    // 
    handleSuccess(response, file, fileList) {
      if (response.flag) {
        //
        this.fileList = [
          {
            url: fileList[0].response.data,
          },
        ];
      }
    },
    // expertId
    async getExpertIdByUserId(id) {
      await this.reqM2Service(`/info/experts/findByUser/${id}`, {}, "get").then(
        (res) => {
          res = res.data;
          if (res.code === 20000) {
            res = res.data;
            this.expertId = res.id;
          } else {
            this.$message({
              message: "",
            });
          }
          this.loading = false;
        }
      );
    },
    //  expertId
    async getArticleListByExpertId(id, page = 1, size = 5) {
      this.page = page;
      this.size = size;
      // await this.reqM2Service(
      //     `/info/diseaseArticles/findByExperts/${id}/${page}/${size}`,
      //     {},
      //     "get"
      //   )
      await this.reqM2Service(
        `/info/diseaseArticles/${page}/${size}`,
        {},
        "post"
      ).then((res) => {
        res = res.data;
        if (res.code === 20000) {
          res = res.data;
          this.total = res.total;
          this.articleList = [];
          res.rows.forEach((item) => {
            // state
            switch (item.state) {
              case 0:
                item.stateInfo = "";
                break;
              case 1:
                item.stateInfo = "";
                break;
              case 2:
                item.stateInfo = "";
                break;
            }
            // 
            item.creationTime = this.formatTime(item.creationTime);
            this.articleList.push(item);
          });
        } else {
          this.$message({
            message: "",
          });
        }
        this.loading = false;
      });
    },
    // 
    formatTime(date) {
      //date
      const d = new Date(date);
      const month =
        d.getMonth() + 1 < 10 ? "0" + (d.getMonth() + 1) : d.getMonth() + 1;
      const day = d.getDate() < 10 ? "0" + d.getDate() : d.getDate();
      const hours = d.getHours() < 10 ? "0" + d.getHours() : d.getHours();
      const min = d.getMinutes() < 10 ? "0" + d.getMinutes() : d.getMinutes();
      const sec = d.getSeconds() < 10 ? "0" + d.getSeconds() : d.getSeconds();
      const times =
        d.getFullYear() +
        "-" +
        month +
        "-" +
        day +
        " " +
        hours +
        ":" +
        min +
        ":" +
        sec;
      return times;
    },
  },
  async mounted() {
    // expertId
    await this.getExpertIdByUserId(this.$store.state.userData.userId);
    // expertId
    await this.getArticleListByExpertId(this.expertId);
    // console.log(JSON.parse(JSON.stringify(this.articleList)));
  },
};
</script>
<style  lang="less" scoped>
.articleManage {
  .title {
    position: relative;
    text-align: center;
    font-size: 18px;
    padding: 10px 0;
    font-weight: 500;
    .addArticle {
      position: absolute;
      bottom: 3px;
      right: 0;
    }
  }
}
</style>