<template>
  <div class="lxl-body">
    <div class="lxl-box">
      <!--  -->
      <div class="navigationSearch">
        <div>
          <el-breadcrumb
            separator-class="el-icon-arrow-right"
            class="lxl-breadcrumb"
          >
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <!--  -->
        <div class="search">
          <el-input
            type="text"
            v-model="SearchKey"
            placeholder=""
          ></el-input>
          <i class="el-icon-search" @click="ToSearch(SearchKey)"></i>
        </div>
      </div>
      <el-divider></el-divider>
      <el-container>
        <el-aside style="width: 70%">
          <!--  -->
          <div
            class="topPicture"
            style="margin-bottom: 8px"
            v-loading="loading"
          >
            <div
              class="topPictureson"
              v-for="(item, index) in pagelist.slice(0, 3)"
              :key="index"
              @click="TonewPath(item.id)"
            >
              <el-image :src="item.picture"></el-image>
              <span>{{ item.title }}</span>
            </div>
          </div>
          <!--   -->
          <div>
            <span style="font-size: 15px"></span>
            <el-button
              type="success"
              size="mini"
              plain
              v-for="(item, index) in TypeDataList.slice(0, 6)"
              :key="index"
              @click="TypeChange(item.id, item.name)"
              >{{ item.name }}</el-button
            >
          </div>
          <!--  -->
          <div
            style="
              display: flex;
              justify-content: space-between;
              width: 100%;
              margin-bottom: -20px;
              margin-top: 15px;
            "
          >
            <div style="font-weight: 800; font-size: 18.72px">
              <span
                style="
                  margin-right: 5px;
                  border-left: 6px solid rgb(93, 183, 60);
                "
              ></span>
              {{ this.queryInfo.TypeName }}
            </div>
            <div
              style="
                font-size: 0.8rem;
                margin-top: 6px;
                color: rgb(93, 183, 60);
              "
            >
              <span
                style="color: #9e9e9e; font-weight: 800"
                @click="ToMorePage(queryInfo.TypeID)"
              >
                
              </span>
              <i class="el-icon-caret-right"></i>
            </div>
          </div>
          <!--  -->
          <el-divider></el-divider>
          <!--    -->
          <div
            class="asidePaging"
            v-for="(item, index) in pagelist"
            :key="index"
            v-loading="loading"
          >
            <div
              class="asidePaggingSon"
              v-if="pagelist.length != 0"
              style="border-bottom: 1px solid rgb(230, 230, 230)"
            >
              <!--  -->
              <div class="paggingPicture" @click="TonewPath(item.id)">
                <el-image :src="item.picture" lazy></el-image>
              </div>
              <!--  -->
              <div class="paggingArticle">
                <div
                  class="paggingTitle"
                  @click="TonewPath(item.id)"
                  style="width: 100%; margin-top: 18px"
                >
                  <h2 @click="TonewPath(item.id)">{{ item.title }}</h2>
                </div>
                <div class="paggingContent">
                  <span style="width: 100%" @click="TonewPath(item.id)">
                    {{ item.summary }}
                  </span>
                </div>
                <span class="paggingDetail" @click="TonewPath(item.id)"
                  >[]</span
                >
                <div class="paggingBottom" style="width: 100%">
                  <p style="font-size: 13px; float: left">
                    {{ item.creationTime | timefilters
                    }}<span style="margin-left: 15px">
                       {{ item.clickNum }}</span
                    >
                  </p>
                  <p style="font-size: 13px; right: 10px">
                    <span style="color: green">{{
                      queryInfo.TypeName
                    }}</span>
                  </p>
                </div>
              </div>
            </div>
          </div>
          <!--  -->
          <el-pagination
            background
            layout="prev, pager, next"
            :total="queryInfo.total"
            :page-size="queryInfo.pagesize"
            :current-page="queryInfo.Currentpage"
            @current-change="handleCurrentChange"
            style="
              display: flex;
              justify-content: center;
              margin: 20px 0 20px 0;
            "
          >
          </el-pagination>
        </el-aside>
        <el-main style="width: 30%">
          <div>
            <!--  -->
            <h3>
              <div>
                <span
                  style="
                    margin-right: 5px;
                    border-left: 6px solid rgb(93, 183, 60);
                  "
                ></span>
                <el-tag
                  type="danger"
                  style="margin-left: 5px"
                  size="small"
                  >New</el-tag
                >
              </div>
              <div class="toMore">
                <span @click="ToMorePage(queryInfo.TypeID)">  </span>
                <i class="el-icon-caret-right"></i>
              </div>
            </h3>
            <ul class="ccy-css">
              <li
                v-for="(item, index) in caifuList"
                :key="index"
                @click="TonewPath(item.id)"
              >
                {{ item.title }}
              </li>
            </ul>
            <!--  -->
            <h3>
              <div>
                <span
                  style="
                    margin-right: 5px;
                    border-left: 6px solid rgb(93, 183, 60);
                  "
                ></span>
                <el-tag
                  style="margin-left: 5px"
                  type="danger"
                  size="small"
                  ></el-tag
                >
              </div>
              <div class="toMore">
                <span @click="ToMorePage(queryInfo.TypeID)">  </span>
                <i class="el-icon-caret-right"></i>
              </div>
            </h3>
            <ul class="ccy-css">
              <li
                v-for="(item, index) in hangqingList"
                :key="index"
                @click="TonewPath(item.id)"
              >
                {{ item.title }}
              </li>
            </ul>
            <!--  -->
            <h3>
              <div>
                <span
                  style="
                    margin-right: 5px;
                    border-left: 6px solid rgb(93, 183, 60);
                  "
                ></span>
                <el-tag
                  type="danger"
                  size="small"
                  style="margin-left: 5px"
                  >Hot</el-tag
                >
              </div>
              <div class="toMore">
                <span @click="ToMorePage(queryInfo.TypeID)">  </span>
                <i class="el-icon-caret-right"></i>
              </div>
            </h3>
            <ul class="ccy-css">
              <li
                v-for="(item, index) in chanlianList"
                :key="index"
                @click="TonewPath(item.id)"
              >
                {{ item.title }}
              </li>
            </ul>
            <!---->   
            <!---->
            <div class="drive" style="width: 100%"></div>         
          </div>
          <div class="asideBottom">
            <h3 style="margin-bottom:5px">
              <div>
                <span
                  style="
                    margin-right: 5px;
                    border-left: 6px solid rgb(93, 183, 60);
                  "
                ></span>
                <el-tag
                  type="danger"
                  size="small"
                  style="margin-left: 5px"
                  >hot</el-tag
                >
              </div>
              <div class="toMore">
                <span @click="ToMorePage(queryInfo.TypeID)">  </span>
                <i class="el-icon-caret-right"></i>
              </div>
            </h3>
            <div class="tageson">
              <el-button size="medium" round></el-button>
              <el-button size="medium" round></el-button>
              <el-button size="medium" round></el-button>
            </div>
            <div class="tageson">
              <el-button size="medium" round></el-button>
              <el-button size="medium" round></el-button>
            </div>
            <div class="tageson">
              <el-button size="medium" round></el-button>
              <el-button size="medium" round></el-button>
            </div>
            <div class="tageson">
              <el-button size="medium" round></el-button>
              <el-button size="medium" round></el-button>
            </div>
          </div>
        </el-main>
      </el-container>
    </div>
  </div>
</template>
<script>
export default {
  //
  filters: {
    timefilters(val) {
      if (val == null || val == "") {
        return "";
      } else {
        let d = new Date(val); //val 
        let month =
          d.getMonth() + 1 < 10 ? "0" + (d.getMonth() + 1) : d.getMonth() + 1;
        let day = d.getDate() < 10 ? "0" + d.getDate() : d.getDate();
        let hours = d.getHours() < 10 ? "0" + d.getHours() : d.getHours();
        let min = d.getMinutes() < 10 ? "0" + d.getMinutes() : d.getMinutes();
        let sec = d.getSeconds() < 10 ? "0" + d.getSeconds() : d.getSeconds();
        let times =
          d.getFullYear() +
          "-" +
          month +
          "-" +
          day +
          " " +
          hours +
          ":" +
          min +
          ":" +
          sec;
        return times;
      }
    },
  },
  data() {
    return {
      //
      queryInfo: {
        Currentpage: 1, //
        pagesize: 5, //
        total: null, //
        TypeID: "1321798002377101312",
        TypeName: "",
      },
      // 
      pagelist: [],
      //  1321798002377101312  ID
      xiahang: "1321798002377101312",
      duixiaId: "1320625468222869504",
      //
      caifuList: [],
      caifuID: "1316743601669148672",
      //
      hangqingList: [],
      hangqingId: "1316745747953225728",
      //
      chanlianList: [],
      chanlianId: "1320546102558199808",
      //
      TypeDataList: [],
      SearchKey: "",
      loading: true,
    };
  },
  created() {
    this.getPageList(this.queryInfo.TypeID); //   
    this.getTypeData(); //
    this.caifuList = this.getTypeInfo(this.caifuID, 9); //
    this.getTypeInfo(this.hangqingId, 7); //
    this.getTypeInfo(this.chanlianId, 6); //
  },
  methods: {
    //
    TonewPath(id) {
      this.$router.push({
        path: "/instructdetail",
        query: { id: id },
      });
    },
    //   ID
    ToMorePage(path) {
      this.$router.push({
        path: "/instructpagedetail",
        query: { path: path },
      });
    },
    // 
    ToSearch(SearchKey) {
      this.$router.push({
        path: "/instructpagedetail",
        query: { SearchKey: SearchKey },
      });
    },
    //ID  
    async getTypeInfo(TypeNum, size) {
      try {
        const { data: res } = await this.reqM2Service(
          `/info/shrimpIndustry/search/searchByTypeId/${TypeNum}/1/${size}`,
          "",
          "post"
        );
        if (res.code === 20000) {
          if (res.data.rows.length == 0) {
            console.log("");
          } else {
            if (size == 9) {
              //
              this.caifuList = res.data.rows.sort((a, b) => {
                return a.clickNum - b.clickNum;
              });
            } else if (size == 7) {
              //
              this.hangqingList = res.data.rows.sort((a, b) => {
                return a.clickNum - b.clickNum;
              });
            } else if (size == 6) {
              //
              console.log("");
              console.log(res);
              this.chanlianList = res.data.rows.sort((a, b) => {
                return a.clickNum - b.clickNum;
              });
            }
          }
        } else {
          console.log("");
        }
      } catch (error) {
        console.log("");
      }
    },
    // ID   
    async getPageList(TypeID) {
      try {
        const { data: res } = await this.reqM2Service(
          `/info/shrimpIndustry/search/searchByTypeId/${TypeID}/${this.queryInfo.Currentpage}/${this.queryInfo.pagesize}`,
          "",
          "post"
        );
        if (res.code === 20000) {
          console.log(res);
          this.pagelist = res.data.rows;
          this.queryInfo.total = res.data.total;
          this.loading = false;
        } else {
          console.log("");
        }
      } catch (error2) {
        console.log("");
      } 

    },
    //
    handleCurrentChange(newpage) {
      this.loading = true;
      this.queryInfo.Currentpage = newpage;
      this.getPageList(this.queryInfo.TypeID);
    },
    //
    async getTypeData() {
      try {
        const { data: res } = await this.reqM2Service(
          "/info/shrimpIndustryTypes",
          "",
          "get"
        );
        if (res.code === 20000) {
          this.TypeDataList = res.data;
        } else {
          console.log("");
        }
      } catch (error3) {
        console.log("");
      }
    },
    TypeChange(ChangeID, ChangeName) {
      this.queryInfo.Currentpage = 1;
      this.queryInfo.TypeID = ChangeID;
      this.queryInfo.TypeName = ChangeName;
      this.loading = true;
      this.getPageList(ChangeID);
    },
  },
};
</script>
<style lang="less" scoped>
.lxl-body {
  display: flex;
  justify-content: center; //-
  min-width: 1150px;
}
.navigationSearch {
  height: 40px;
  display: flex;
  justify-content: space-between;
  .search {
    position: relative;
    i {
      top: 13px;
      cursor: pointer;
      position: absolute;
      right: 20px;
    }
  }
}
.ccy-css {
  color: #858585;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-size: 13px;
  margin-top: -15px;
  margin-bottom: 6px;
}
.ccy-css > li:hover {
  color: black;
  font-weight: 800px;
}
.ccy-css:first-line {
  color: black;
  text-decoration: none;
  text-overflow: ellipsis;
  font-size: 15px;
  font-weight: 700;
}
.el-image {
  cursor: pointer !important;
}
span {
  cursor: pointer;
}
.el-container {
  display: flex;
  justify-content: space-between;
  .el-aside {
    flex-direction: column;
    .topPicture {
      width: 100%;
      display: flex;
      height: 170px;
      justify-content: space-between;
      .topPictureson {
        height: 100%;
        width: 33%;
        position: relative;
        display: flex;
        .el-image {
          width: 98%;
          height: 90%;
        }
        span {
          text-align: center;
          background-color: #333;
          position: absolute;
          width: 98%;
          opacity: 0.7;
          bottom: 19px;
          text-overflow: ellipsis;
          overflow: hidden;
          white-space: nowrap;
          font-size: 13.5px;
        }
      }
    }
    .asidePaging {
      //
      display: flex;
      margin-top: -22px;
      .asidePaggingSon {
        width: 100%;
        display: flex;
        height: 190px;
        position: relative;
        .paggingPicture {
          height: 90%;
          width: 29%;
          .el-image {
            margin-top: 25px;
            width: 100%;
            height: 90%;
          }
        }
        // 
        .paggingArticle {
          width: 60%;
          margin: 10px 0 0 10px;
          h2 {
            cursor: pointer;
            text-overflow: ellipsis;
            overflow: hidden;
            font-size: 18.72px;
            margin-bottom: 5px;
            white-space: nowrap;
          }
          .paggingContent {
            font-size: 13px;
            text-overflow: ellipsis;
            overflow: hidden;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 4;
          }
          .paggingDetail {
            color: green;
          }
          .paggingDetail:hover {
            color: orange;
          }
          .paggingBottom {
            p {
              position: absolute;
              bottom: 3px;
            }
          }
        }
      }
    }
  }
  .el-main {
    margin-top: -20px;
    h3 {   
      display: flex;
      justify-content: space-between;
      .toMore {
        //
        font-size: 0.8rem;
        margin-top: 6px;
        color: rgb(93, 183, 60);
        span {
          color: #9e9e9e;
        }
      }
    }
    li {
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
      padding-bottom: 2px;
      list-style-position: inside;
      width: 250px;
      cursor: pointer;
      margin-bottom: 3px;
    }
    .asideBottom {
      margin-bottom: 15px;
      .el-button {
        margin-bottom: 8px;
        background-color: rgb(240, 249, 235);
        color: green;
      }
    }
  }
}
.contentShow {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}
</style>
