<template>
  <div class="expertOne" v-loading="loading">
    <div class="title">
      
      <div class="addArticle">
        <el-button type="primary" size="medium" @click="handleUpdate"
          ></el-button
        >
      </div>
    </div>
    <el-card shadow="always">
      <span>{{ expertInfo.name }}</span>
    </el-card>
    <el-card shadow="always">
      <span>{{ expertInfo.state == 1 ? "" : "" }}</span>
    </el-card>
    <el-card shadow="always">
      <span>{{ expertInfo.state == 1 ? "" : "" }}</span>
    </el-card>
    <el-card shadow="always">
      <span>{{ expertInfo.goodAt }}</span>
    </el-card>
    <el-card shadow="always">
      <span>{{ expertInfo.onlineTime }}</span>
    </el-card>
    <el-card shadow="always">
      <span>{{ expertInfo.address }}</span>
    </el-card>
    <!-- -- -->
    <el-dialog title="" :visible.sync="dialogFormVisibleUpdate">
      <el-row>
        <el-col :span="18">
          <el-form
            :model="ruleForm"
            :rules="rules"
            ref="ruleForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item :label="label.userId" prop="id">
              <el-input
                v-model.number="ruleForm.userId"
                placeholder=""
                disabled
              ></el-input>
            </el-form-item>
            <el-form-item :label="label.name" prop="name">
              <el-input
                type="textarea"
                autosize
                v-model="ruleForm.name"
                placeholder=""
              ></el-input>
            </el-form-item>
            <el-form-item :label="label.introduction" prop="introduction">
              <el-input
                v-model="ruleForm.introduction"
                placeholder=""
              ></el-input>
            </el-form-item>
            <el-form-item :label="label.goodAt" prop="goodAt">
              <el-input
                type="textarea"
                autosize
                v-model="ruleForm.goodAt"
                placeholder=""
              ></el-input>
            </el-form-item>
            <el-form-item
              :label="label.onlineTime"
              prop="onlineTime"
            >
              <el-input
                v-model="ruleForm.onlineTime"
                placeholder=""
              ></el-input>
            </el-form-item>
            <el-form-item :label="label.typeId" prop="typeId">
              <el-select v-model="ruleForm.typeId" placeholder="">
                <el-option
                  v-for="item in options"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item :label="label.address" prop="address">
              <el-input
                v-model="ruleForm.address"
                placeholder=""
              ></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-upload
                :span="2"
                class="upload-demo"
                action="http://106.75.154.40:9005/information/upload"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                :on-success="handleSuccess"
                :file-list="fileList"
                list-type="picture"
              >
                <el-button
                  size="small"
                  type="primary"
                  v-if="!fileList[0]"
                  class="btnTop"
                  ></el-button
                >
                <div slot="tip" class="el-upload__tip">
                  jpg/png500kb
                </div>
              </el-upload>
            </el-form-item>
          </el-form>
          <div class="form-button" style="text-align:center;">
            <el-button type="success" @click="submitForm('ruleForm')"
              ></el-button
            >
          </div>
        </el-col>
      </el-row>
    </el-dialog>
    <!-- -- -->
  </div>
</template>
<script>
export default {
  data() {
    return {
      userId: "",
      loading: true,
      expertInfo: {},
      label: {
        address: "",
        goodAt: "",
        introduction: "",
        name: "",
        onlineTime: "",
        picture: "",
        typeId: "",
        userId: "id",
      },

      // 
      fileList: [],
      // 
      options: [],
      // 
      dialogFormVisibleUpdate: false,
      // 
      ruleForm: {},
      // 
      rules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        goodAt: [
          { required: true, message: "", trigger: "blur" },
        ],
        onlineTime: [
          { required: true, message: "", trigger: "blur" },
        ],
        address: [{ required: true, message: "", trigger: "blur" }],
        typeId: [
          {
            required: true,
            message: "id",
            trigger: "change",
          },
        ],
      },
    };
  },
  methods: {
    // 
    submitForm(formName) {
      // 
      if (!this.fileList[0].url) {
        this.$message({
          message: "",
        });
        return;
      }
      this.ruleForm.picture = this.fileList[0].url;
      console.log("this.ruleForm: ", this.ruleForm);
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.updateExpertInfo(this.ruleForm);
        } else {
          this.$message({
            message: "!",
          });
          return false;
        }
      });
    },
    // 
    updateExpertInfo(expertInfo) {
      // this.$http
      //   .put(
      //     `http://106.75.154.40:9005/experts/updateExperts/${expertInfo.id}`,
      //     expertInfo
      //   )
      this.reqM8Service(`/experts/updateExperts/${expertInfo.id}`,expertInfo,'put')
        .then((res) => {
          res = res.data;
          if (res.code == 20000) {
            console.log('res: ', res);
            this.dialogFormVisibleUpdate = false;
            // 
            this.getExpertInfoByUserId(this.userId);
            this.$message({
              message: res.message,
            });
          } else {
            this.$message({
              message: "!",
            });
          }
        });
    },
    // 
    async handleUpdate() {
      await this.getTypes();
      this.dialogFormVisibleUpdate = true;
      this.ruleForm = this.expertInfo;
    },
    // 
    handleRemove(file, fileList) {
      this.fileList = fileList;
    },
    // 
    handlePreview(file) {
      console.log(file);
    },
    // 
    handleSuccess(response, file, fileList) {
      if (response.flag) {
        //
        this.fileList = [
          {
            url: fileList[0].response.data,
          },
        ];
      }
    },
    // 
  async getTypes() {
    await this.reqM2Service(`/info/expertsType`, {}, "get")
      .then((res) => {
        res = res.data;
        // console.log('res: ', res);
        if (res.code === 20000) {
          res = res.data;
          return res;
        } else {
          this.$message({
            message: "",
          });
        }
      })
      .then((options) => {
        this.options = options;
        // typeid
        this.options.some((item) => {
          // console.log('item: ', item.id,'this.ruleForm.typeId: ', this.ruleForm.typeId);
          if (item.id == this.expertInfo.typeId) {
            this.ruleForm.typeId = item.name;
            return true;
          }
          return false;
        });
      });
  },
    // id
    getExpertInfoByUserId(id) {
      this.reqM2Service
        (`/info/experts/findByUser/${id}`,{},'get')
        .then((res) => {
          res = res.data;
          if (res.code === 20000) {
            res = res.data;
            this.expertInfo = res;
            this.fileList.push({url:this.expertInfo.picture})
            console.log('res: ', res);
          } else {
            this.$message({
              message: "",
            });
          }
          this.loading = false;
        });
    },
  },
  
  mounted() {
    this.userId = this.$store.state.userData.userId;
    // console.log('this.userId: ', this.userId);
    this.getExpertInfoByUserId(this.userId);
  },
};
</script>
<style lang="less" scoped>
.expertOne {
  /deep/.title {
    position: relative;
    text-align: center;
    font-size: 18px;
    padding: 10px 0;
    font-weight: 500;
  }
  .addArticle {
    position: absolute;
    top: 0;
    right: 0;
  }
}
</style>