<template>
    <div class="lxl-body">
      <div class="lxl-box">
        <!--  -->
        <el-breadcrumb
          separator-class="el-icon-arrow-right"
          class="lxl-breadcrumb"
        >
          <el-breadcrumb-item></el-breadcrumb-item>
          <el-breadcrumb-item :to="{ path: '/emall' }"></el-breadcrumb-item>
          <el-breadcrumb-item @click.native="goLastPage" style="cursor:pointer; font-weight: 700"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-divider></el-divider>

        <!--  -->
        <div class="goodsIntroduceArea">
            <el-row :gutter="20">
          <!--  -->
          <el-col :span="9">
            <div class="block">
              <el-carousel height="300px">
                <el-carousel-item v-for="(item, i) in cateItem.productImages" :key="i">
                  <el-image :src="item"></el-image>
                </el-carousel-item>
              </el-carousel>
            </div>
          </el-col>

          <!--  -->
          <el-col :span="15">
            <!--  -->
            <div>
              <el-tag effect="dark" type="danger" size="mini"></el-tag>
              <span class="goodsTitle">{{ cateItem.productTitle }}</span>
            </div>
            <!--  -->
            <div class="updateTime">{{ cateItem.productUpdated }}</div>
            <!--  -->
            <div class="priceBackground">
              <span style="font-size: 14px; color: #aaa"></span>
              <span style="font-size: 24px; color: red">{{ cateItem.productPrice }}</span>
            </div>
            <!--  -->
            <div class="address">
              <span style="color: #aaa"></span>
              <span>{{ cateItem.productArea }}</span>
            </div>
            <el-divider></el-divider>
            <!--  -->
            <div>
              <el-row>
                <el-col :span="11" class="numInfo"><span class="number">{{ cateItem.productStars }}</span></el-col>
                <el-col :span="2" class="numInfo">|</el-col>
                <el-col :span="11" class="numInfo"><span class="number">{{ comment.length }}</span></el-col>
              </el-row>
            </div>
            <el-divider></el-divider>
            <!--  -->
            <!-- <div>
              <el-row>
                <el-col :span="3" style="font-size: 14px"></el-col>
                <el-col :span="21" class="classButton">
                    <el-button plain></el-button>
                    <el-button plain></el-button>
                    <el-button plain></el-button>
                    <el-button plain></el-button>
                    <el-button plain></el-button>
                    <el-button plain></el-button>
                    <el-button plain></el-button>
                </el-col>
              </el-row>
            </div> -->
            <!--  -->
            <div class="buyButton">
              <el-button style="margin: 10px 20px 0 0" @click="addCart"></el-button>
              <el-button type="primary" @click="buy"></el-button>
            </div>
          </el-col>

        </el-row>
        </div>
        <!--  -->
        <el-divider></el-divider>

        <!--  -->
        <div>
          <el-row>
            <el-col :span="5" class="card">
              <el-card class="box-card">
                <div slot="header" class="clearfix">
                  <span style="font-weight: 700"></span>
                </div>
                <div class="userInfo">
                  <el-avatar :src="merchant.merchantLogo"></el-avatar>
                  <span style="cursor: pointer;" @click="goToShopDetail">{{ merchant.merchantName }}</span>
                </div>
                <div class="merchantInfo">
                  <div>
                    <span></span>
                    <span>{{ merchant.merchantDesc }}</span>
                  </div>
                  <div>
                    <span></span>
                    <span>{{ merchant.merchantProvince }}{{ merchant.merchantCity }}{{ merchant.merchantTown }}{{ merchant.merchantAddress }}</span>
                  </div>
                  <div>
                    <span></span>
                    <span>{{ merchant.merchantPhone }}</span>
                  </div>
                <div>
                </div>
                </div>
              </el-card>
              <el-card :body-style="{ padding: '5px' }">
                  <div slot="header" class="clearfix">
                    <span></span>
                  </div>
                  <div class="otherCard" v-for="(item, i) in merchantList" :key="i">
                    <img :src="goodsArray[0]" class="image" @click="goToGoodsDetail(item)">
                    <span @click="goToGoodsDetail(item)">{{ item.productTitle | ellipsis}}</span>
                    <div class="otherPrice">{{ item.productPrice }}{{ item.productUnit }}</div>
                  </div>
                </el-card>
            </el-col>

            <!--  -->
            <el-col :span="19">
              <div class="goodsDetail">
                <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
                  <el-tab-pane label="" name="first">
                    <div class="goodsAttibute">
                      <h4 style="
                  margin-right: 5px;
                  border-left: 6px solid #409EFF;
                "></h4>
                      <span>{{ cate.categoryName }}</span>
                      <span> {{ cateItem.productNum }}</span>
                      <!-- <span></span>
                      <span></span> -->
                      <span>{{ cateItem.productArea }}</span>
                      <h4 style="
                  margin-right: 5px;
                  margin-top: 25px;
                  border-left: 6px solid #409EFF;
                "></h4>
                    <div class="productIntroduce">{{ cateItem.productTitle }}</div>
                    <el-image v-for="(item, i) in cateItem.productImages" :key="i" :src="item"></el-image>
                    </div>
                    
                  </el-tab-pane>
                  <el-tab-pane label="" name="second">
                    <div class="comment">
                      <el-divider></el-divider>
                      <el-row>
                        <el-col :span="3">
                          <el-avatar src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"></el-avatar>
                        </el-col>
                        <el-col :span="21">111</el-col>
                      </el-row>
                    <el-divider></el-divider>
                    </div>
                  </el-tab-pane>
                </el-tabs>
              </div>
            </el-col>
          </el-row>
        </div>
        <el-dialog
          title=""
          :visible.sync="cartDialogVisible"
          width="30%">
            <p></p>
            <span slot="footer" class="dialog-footer">
              <el-button @click="cartDialogVisible = false"> </el-button>
              <el-button type="primary" @click="addToCart"> </el-button>
            </span>
        </el-dialog>
      </div>
    </div>
</template>

<script>
export default {
    data() {
      return {
        cateItem: [],
        // 
        comment: [],
        // 
        cate: [],
        goodsArray: [],
        // 
        merchantList: [],
        // 
        merchant: [],
        imgUrl: [
            { url: 'https://cube.elemecdn.com/6/94/4d3ea53c084bad6931a56d5158a48jpeg.jpeg' },
            { url: 'https://fuss10.elemecdn.com/1/34/19aa98b1fcb2781c4fba33d850549jpeg.jpeg' },
            { url: 'https://fuss10.elemecdn.com/a/3f/3302e58f9a181d2509f3dc0fa68b0jpeg.jpeg' },
            { url: 'https://cube.elemecdn.com/6/94/4d3ea53c084bad6931a56d5158a48jpeg.jpeg' },
        ],
        ImgUrl: 'https://cube.elemecdn.com/6/94/4d3ea53c084bad6931a56d5158a48jpeg.jpeg',
        activeName: 'first',
        // 
        flag: false,
        cartDialogVisible: false,
        // 
        cartItem: {
          productId: '',
          userId: '',
          num: 0,
          price: 1,
          picture: '',
          categoryId: ''
        }
      }
    },
    created() {
      this.getItem()
      // 
      this.getcomment()
      // 
      this.getMerchantGoods()
      // 
      this.getMerchant()
    },
    methods: {
      goLastPage() {
        this.$router.go(-1)
      },
      handleClick() {},
      getIndex(imgUrl){
        this.ImgUrl = imgUrl;
      },
      goToGoodsDetail(item) {
        this.$router.push({
          path: '/emallDetail',
          name: 'emallDetail',
          query: item
        })
        window.location.reload()
      },
      goToShopDetail() {
        this.$router.push({
          path: '/shopDetail',
          query: this.merchant
        })
      },
      async getItem() {
        this.cateItem = this.$route.query
        if (typeof this.cateItem.productImages == 'string') {
          this.cateItem.productImages = this.cateItem.productImages.split(",");
        }
        console.log(this.cateItem)
        // 
        const { data: res } = await this.reqM4Service(
          `/category/${this.cateItem.categoryId}`,
          "",
          "get"
        );
        // console.log(res)
        if (res.code !== 20000) {
          return this.$message.error("");
        }
        this.cate = res.data
      },
      async getcomment() {
        const { data: res } = await this.reqM4Service(
          `/comment/${this.cateItem.productId}/1/10`,
          "",
          "get"
        );
        // console.log(res)
        if (res.code !== 20000) {
          return this.$message.error("");
        }
        this.comment = res.data.content
      },
      // 
      async getMerchantGoods() {
        const { data: res } = await this.reqM4Service(
          `/product/queryProducts/${this.cateItem.merchantId}/1/3`,
          "",
          "get"
        );
        // console.log(res)
        if (res.code !== 20000) {
          return this.$message.error("");
        }
        this.merchantList = res.data.content
        this.merchantList.forEach(element => {
          this.goodsArray = element.productImages.split(',')
        });
      },
      // 
      async getMerchant() {
        const { data: res } = await this.reqM4Service(
          `/merchant/${this.cateItem.merchantId}`,
          "",
          "get"
        );
        // console.log(res)
        if (res.code !== 20000) {
          return this.$message.error("");
        }
        this.merchant = res.data
      },
      checkLogin() {
        const tokenStr = window.sessionStorage.getItem('token')
        if(tokenStr) {
          this.flag = true
        } else {
          this.flag = false
          var result = confirm('')
          if (result === true) {
            this.$router.push('/login')
          }
        }
      },
      // 
      buy() {
        this.checkLogin()
      },
      // 
      addCart() {
        this.checkLogin()
        if (this.flag == true) {
          console.log(this.flag)
          this.cartDialogVisible = true
        }
      },
      // 
      async addToCart() {
        this.cartItem.productId = this.cateItem.productId
        this.cartItem.userId = JSON.parse(window.sessionStorage.getItem('userData')).userId
        this.cartItem.price = this.cateItem.productPrice
        this.cartItem.picture = this.cateItem.productImages[0]
        this.cartItem.categoryId = this.cateItem.categoryId
        const { data: res } = await this.reqM4Service(
          `/cart`,
          this.cartItem,
          "post"
        );
        // console.log(res)
        if (res.code === 20000) {
          this.$message.success('')
        }
        this.cartDialogVisible = false
      }
    },
    filters: {
        ellipsis(value) {
          if (!value) return "";
          if (value.length > 25) {
            return value.slice(0, 25) + "...";
          }
          return value;
        }
      }
}
</script>

<style lang="less">
.ccy-drvider {
  margin: 10px 0 7px 0;
}
.lxl-body {
  display: flex;
  justify-content: center;
}
.lxl-breadcrumb {
  margin-top: 25px;
  margin-left: 18px;
  margin-bottom: -10px;
}
.lxl-box {
  width: 1150px;
  margin: 0 auto;
}
.goodsIntroduceArea {
  margin-bottom: 80px;
}
.little_img ul {
    list-style: none;
    li {
        float: left;
        margin-top: 20px;
        margin-right: 10px;
        cursor: pointer;
    }
}
.goodsTitle {
  font-weight: 700;
  font-size: 16px;
  margin-left: 10px;
}
.updateTime {
  font-size: 12px;
  color: #aaa;
  margin-top: 5px;
}
.moreButton {
  float: right;
}
.priceBackground {
  width: 100%;
  height: 120px;
  margin-top: 10px;
  background: #ddd;
  span {
    line-height: 120px;
    margin-left: 20px;
  }
}
.address {
  font-size: 14px;
  margin-top: 10px;
}
.numInfo {
  text-align: center;
  color: #aaa;
}
.number {
  color: orange
}
.classButton {
  .el-button {
    width: 170px;
    text-align: center;
    margin-left: 10px;
    margin-top: 10px;
  }
}
.buyButton {
  margin: 30px 0 0 80px;
  .el-button {
    width: 150px;
  }
}
.card {
  .el-card__header {
    padding: 5px 20px;
  }
}
.userInfo {
  height: 40px;
  span {
    vertical-align: middle;
    margin-left: 5px;
  }
}
.otherCard {
  margin-top: 20px;
  .otherPrice {
    color: red;
  }
  .image {
    width: 210px;
    cursor: pointer;
  }
  span {
    cursor: pointer;
  }
}
.goodsDetail {
  .el-tabs {
    margin-left: 20px;
  }
  .el-tabs__item {
    width: 200px;
    text-align: center;
  }
}
.goodsAttibute {
  span {
    display: inline-block;
    width: 200px;
    margin-right: 20px;
    margin-top: 20px;
  }
  .el-image {
    margin-top: 10px;
  }
}
.productIntroduce {
  margin-top: 20px;
  text-indent: 2em;
}
.merchantInfo {
  margin-top: 10px;
  span {
    font-size: 12px;
    color: #aaa;
  }
}
.comment {
  margin-top: 10px;
  margin-left: 20px;
}
.cTitle {
  font-weight: 700;
}
</style>
