<template>
  <div class="lxl-card">
    <div>
      <el-button
        v-if="!isApplying && !isExpert"
        type="primary"
        @click="applyToBeExpert()"
        plain
        ></el-button
      >
      <el-button v-if="isExpert" type="primary" @click="toExpertManage()" plain
        ></el-button
      >
      <div v-else-if="isApplying">
        <h3></h3>
      </div>
    </div>
    <!-- -- -->
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-row>
        <el-col :span="18">
          <el-form
            :model="ruleForm"
            :rules="rules"
            ref="ruleForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item :label="label.userId" prop="userId">
              <el-input
                v-model.number="ruleForm.userId"
                placeholder=""
                disabled
              ></el-input>
            </el-form-item>
            <el-form-item :label="label.name" prop="name">
              <el-input
                type="textarea"
                autosize
                v-model="ruleForm.name"
                placeholder=""
              ></el-input>
            </el-form-item>
            <el-form-item :label="label.introduction" prop="introduction">
              <el-input
                v-model="ruleForm.introduction"
                placeholder=""
              ></el-input>
            </el-form-item>
            <el-form-item :label="label.goodAt" prop="goodAt">
              <el-input
                type="textarea"
                autosize
                v-model="ruleForm.goodAt"
                placeholder=""
              ></el-input>
            </el-form-item>
            <el-form-item :label="label.onlineTime" prop="onlineTime">
              <el-input
                v-model="ruleForm.onlineTime"
                placeholder=""
              ></el-input>
            </el-form-item>
            <el-form-item :label="label.typeId" prop="typeId">
              <el-select v-model="ruleForm.typeId" placeholder="">
                <el-option
                  v-for="item in options"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item :label="label.address" prop="address">
              <el-input
                v-model="ruleForm.address"
                placeholder=""
              ></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-upload
                :span="2"
                class="upload-demo"
                action="http://106.75.154.40:9005/information/upload"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                :on-success="handleSuccess"
                :file-list="fileList"
                list-type="picture"
              >
                <el-button
                  size="small"
                  type="primary"
                  v-if="!fileList[0]"
                  class="btnTop"
                  ></el-button
                >
                <div slot="tip" class="el-upload__tip">
                  jpg/png500kb
                </div>
              </el-upload>
            </el-form-item>
          </el-form>
          <div class="form-button" style="text-align: center">
            <el-button type="success" @click="submitForm('ruleForm')"
              ></el-button
            >
          </div>
        </el-col>
      </el-row>
    </el-dialog>
    <!-- -- -->
  </div>
</template>
<script>
export default {
  data() {
    return {
      isExpert: false,
      isApplying: false,
      isRefused: false,
      userId: "",
      // 
      label: {
        address: "",
        goodAt: "",
        introduction: "",
        name: "",
        onlineTime: "",
        picture: "",
        typeId: "",
        userId: "",
      },

      // 
      fileList: [],
      // 
      options: [],
      // 
      dialogFormVisible: false,
      // 
      ruleForm: {
        userId: "",
        name: "",
        introduction: "",
        goodAt: "",
        onlineTime: "",
        typeId: "",
        address: "",
      },
      // 
      rules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        goodAt: [
          { required: true, message: "", trigger: "blur" },
        ],
        onlineTime: [
          { required: true, message: "", trigger: "blur" },
        ],
        address: [{ required: true, message: "", trigger: "blur" }],
        typeId: [
          {
            required: true,
            message: "id",
            trigger: "change",
          },
        ],
      },
    };
  },
  methods: {
    // 
    toExpertManage() {
      this.$router.push("/expertSystemManage");
    },
    // 
    async applyToBeExpert() {
      await this.getTypes();
      this.dialogFormVisible = true;
    },
    // userId
    async judgeUserIsExpert(id) {
      let flag = false;
      await this.reqM2Service(`/info/experts/isExperts/${id}`, {}, "get").then(
        (res) => {
          res = res.data;
          if (res.code == 20000) {
            // console.log("res: ", res);
            flag = res.flag;
          }
        }
      );
      return flag;
    },
    // userId
    async judgeUserIsAppliedExpert(id) {
      let flag = false;
      await this.reqM2Service(`/info/experts/isApplying/${id}`, {}, "get").then(
        (res) => {
          res = res.data;
          if (res.code == 20000) {
            // console.log("res: ", res);
            flag = res.flag;
          }
        }
      );
      return flag;
    },
    // userId
    async judgeUserIsRefused(id) {
      let flag = false;
      await this.reqM2Service(`/info/experts/isRefused/${id}`, {}, "get").then(
        (res) => {
          res = res.data;
          if (res.code == 20000) {
            // console.log("res: ", res);
            flag = res.flag;
          }
        }
      );
      return flag;
    },
    // 
    addExpertInfo(params) {
      this.reqM2Service("/info/experts/add", params, "post").then((res) => {
        res = res.data;
        if (res.code == 20000) {
          this.$message({
            type: "success",
            message: "",
          });
          this.showWhatContent();
          this.dialogFormVisible = false;
        } else {
          this.$message({
            type: "error",
            message: "",
          });
        }
      });
    },
    // 
    submitForm(formName) {
      // 
      if (!this.fileList[0].url) {
        this.$message({
          message: "",
        });
        return;
      }
      this.ruleForm.picture = this.fileList[0].url;
      console.log("this.ruleForm: ", this.ruleForm);
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.addExpertInfo(this.ruleForm);
          this.isApplying = true;
        } else {
          this.$message({
            message: "!",
          });
          return false;
        }
      });
    },
    // 
    handleRemove(file, fileList) {
      this.fileList = fileList;
    },
    // 
    handlePreview(file) {
      console.log(file);
    },
    // 
    handleSuccess(response, file, fileList) {
      if (response.flag) {
        //
        this.fileList = [
          {
            url: fileList[0].response.data,
          },
        ];
      }
    },
    // 
    async getTypes() {
      await this.reqM2Service(`/info/expertsType`, {}, "get")
        .then((res) => {
          res = res.data;
          // console.log('res: ', res);
          if (res.code === 20000) {
            res = res.data;
            return res;
          } else {
            this.$message({
              message: "",
            });
          }
        })
        .then((options) => {
          this.options = options;
        });
    },
    // 
    async showWhatContent() {
      // 
      let isExpert = await this.judgeUserIsExpert(
        this.$store.state.userData.userId
      );
      // console.log("isExpert: ", isExpert);
      if (isExpert) {
        // 
        this.isExpert = true;
      } else {
        // 
        let isApplying = await this.judgeUserIsAppliedExpert(
          this.$store.state.userData.userId
        );
        console.log("isApplying: ", isApplying);
        if (isApplying) {
          this.isApplying = true;
        } 
        // else {
        //   // 
        //   let isRefused = await this.judgeUserIsRefused(
        //     this.$store.state.userData.userId
        //   );
        //   console.log("isRefused: ", isRefused);
        //   this.isRefused = isRefused;
        // }
      }
    },
  },
  async created() {
    this.userId = this.$store.state.userData.userId;
    this.ruleForm.userId = this.userId;
    console.log(this.$store.state.userData.userId);
    this.showWhatContent();
  },
};
</script>
<style lang="less" scoped>
.lxl-card {
  background-color: #fff;
  display: flex;
  justify-content: center;
  padding: 80px;
}
</style>