<script>
export default {
  //
  filters: {
    timefilters(val) {
      if (val == null || val == "") {
        return "";
      } else {
        let d = new Date(val); //val 
        let month =
          d.getMonth() + 1 < 10 ? "0" + (d.getMonth() + 1) : d.getMonth() + 1;
        let day = d.getDate() < 10 ? "0" + d.getDate() : d.getDate();
        let hours = d.getHours() < 10 ? "0" + d.getHours() : d.getHours();
        let min = d.getMinutes() < 10 ? "0" + d.getMinutes() : d.getMinutes();
        let sec = d.getSeconds() < 10 ? "0" + d.getSeconds() : d.getSeconds();
        let times =
          d.getFullYear() +
          "-" +
          month +
          "-" +
          day +
          " " +
          hours +
          ":" +
          min +
          ":" +
          sec;
        return times;
      }
    },
  },

  data() {
    return {
      //
      // datalist: [],
      searchInput: "",
      //
      weekliList: [],
      queryInfo1: {
        Infopage1: 1,
        Infosize1: 16,
        TypeID1: "1316745747953225728",
      },
      jingcai1List: [],
      jingcai2List: [],
      jingcai3List: [],
      queryInfo2: {
        Infopage2: 1,
        Infosize2: 4,
        Infototal2: null,
        TypeID2: "1320546102558199808",
      },

      //3 
      queryInfo3: {
        //
        Currentpage: 1,
        //
        pagesize: 5,
        //
        total: null,
        TypeID3: "1316743601669148672",
      },

      //3 
      pagelist: [],

      //ID
      TypeIdData: [],

      //
      NewDataList: [],

      //
      MonthDataList: [],

      // 
      RecommList: [],
      // 
      ClickDataList: [],

      SearchKey: "",

      loading: true,
      loading1: true,
      loading2: true,
      loading3: true,
      src:
        "https://cube.elemecdn.com/6/94/4d3ea53c084bad6931a56d5158a48jpeg.jpeg",
    };
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll1); //handleScroll 
  },
  created() {
    // 
    console.log(11111);
    this.getLoadData();
  },
  methods: {
    //
    TonewPath(id) {
      this.$router.push({
        path: "/instructdetail",
        query: { id: id },
      });
    },
    handleScroll1() {
      let a = document.body.scrollTop + document.documentElement.scrollTop;
      if (a > 24 && this.RecommList.length === 0) {
        this.getRecommData();
        this.getjingcai1();
        window.removeEventListener("scroll", this.handleScroll1);
        window.addEventListener("scroll", this.handleScroll2); //2
      }
    },
    handleScroll2() {
      let a = document.body.scrollTop + document.documentElement.scrollTop;
      if (a > 266 && this.MonthDataList.length === 0) {
        this.getjingcai2();
        this.getMonthData();
        window.removeEventListener("scroll", this.handleScroll2);
        window.addEventListener("scroll", this.handleScroll3);
      }
    },
    handleScroll3() {
      let a = document.body.scrollTop + document.documentElement.scrollTop;
      if (a > 570 && this.pagelist.length === 0) {
        this.getjingcai3();
        window.removeEventListener("scroll", this.handleScroll3);
      }
    },
    // 
    getLoadData() {
      this.getClickData();
      this.getNewData();
      this.getWeekData();
    },
    //
    ToMorePage(id) {
      this.$router.push({
        path: "/instructpagedetail",
        query: { id: id },
      });
    },
    // 
    ToSearch(SearchKey) {
      this.$router.push({
        path: "instructpagedetail",
        query: { SearchKey: SearchKey },
      });
    },
    //
    async getClickData() {
      try {
        const { data: res } = await this.reqM2Service(
          "/info/shrimpIndustry/findByClickNum/1/10",
          "",
          "get"
        );
        console.log("");
        console.log(res)
        if (res.code === 20000) {
          this.ClickDataList = res.data.rows;
        } else {
          console.log(" 20001");
        }
      } catch (error) {
        console.log(" 19999");
      }
    },
    //
    async getWeekData() {
      try {
        const { data: res } = await this.reqM2Service(
          "/info/shrimpIndustry/findByClickWeekly/1/7",
          "",
          "get"
        );
        if (res.code === 20000) {
          console.log("");
          this.weekliList = res.data.rows;
        }
      } catch (error) {
        console.log(error);
      }
    },
    // 1        1320546102558199808
    async getjingcai1() {
      try {
        const { data: res } = await this.reqM2Service(
          `/info/shrimpIndustry/search/searchByTypeId/${this.queryInfo1.TypeID1}/${this.queryInfo1.Infopage1}/${this.queryInfo1.Infosize1}`,
          "",
          "post"
        );
        if (res.code === 20000) {
          console.log("");
          this.jingcai1List = res.data.rows;
        }
      } catch (error) {
        console.log("1999");
      }
    },
    //2 1320648223462920192
    async getjingcai2() {
      try {
        const { data: res } = await this.reqM2Service(
          `/info/shrimpIndustry/findByClickAndType/${this.queryInfo2.TypeID2}/${this.queryInfo2.Infopage2}/${this.queryInfo2.Infosize2}`,
          "",
          "get"
        );
        if (res.code === 20000) {
          console.log("2");
          this.jingcai2List = res.data.rows;
          this.loading2 = false;
        } else {
          console.log("20001");
        }
      } catch (error) {
        console.log("19999");
      }
    },
    // ID 3  
    async getjingcai3() {
      try {
        const { data: res } = await this.reqM2Service(
          `/info/shrimpIndustry/findByClickAndType/${this.queryInfo3.TypeID3}/${this.queryInfo3.Currentpage}/${this.queryInfo3.pagesize}`,
          "",
          "get"
        );
        if (res.code === 20000) {
          console.log("3");
          this.pagelist = res.data.rows;
          this.queryInfo3.total = res.data.total;
          this.loading3 = false;
          window.removeEventListener("scroll", this.handleScroll3);
        } else {
          this.loading3 = false;
          console.log("3");
        }
      } catch (error) {
        console.log(" 19999");
      }
    },
    handleCurrentChange(newpage) {
      //
      this.loading3 = true;
      this.queryInfo3.Currentpage = newpage;
      this.getjingcai3();
    },
    //  
    async getNewData() {
      try {
        const { data: res } = await this.reqM2Service(
          "/info/shrimpIndustry/findByTime/1/9",
