        );
        console.log("");
        if (res.code === 20000) {
          this.RecommList = res.data.rows;
          this.loading1 = false;
        } else {
          console.log(res.message);
        }
      } catch (error) {
        console.log(error);
      }
    },
    //
    async getMonthData() {
      try {
        const { data: res } = await this.reqM2Service(
          "/info/shrimpIndustry/findByClickMonthly/1/20",
          "",
          "get"
        );
        if (res.code === 20000) {
          console.log("");
          this.MonthDataList = res.data.rows;
          this.loading2 = false;
        } else {
          console.log(" 20001");
        }
      } catch (error) {
        console.log(" 19999");
      }
    },
  },
};
</script>
