<template>
  <div class="miniQuestionDetailCard">
    <div class="que_item">
      <!--  -->
      <div class="wzp-que">
        <div class="que_text" style="position:relative;">
          <div class="up">
            <span class="queicon"></span>
            <span class="quetext"> {{ quesInfo.title }} </span>
            <div class="res_time">
              {{ quesInfo.userName ? quesInfo.userName : "" }}
              {{ this.formatTime(quesInfo.creationTime) }}
            </div>
          </div>
          <div class="demo-image__preview;" style="margin-top: 10px">
            <div
              class="images_content"
              v-if="quesInfo.images ? quesInfo.images.length != 0 : false"
            >
              <el-image
                v-for="(item, index) in quesInfo.images"
                :key="index"
                style="width: 100px; height: 100px; margin-right: 10px"
                :src="item"
                :preview-src-list="quesInfo.images"
                fit="cover"
              >
              </el-image>
            </div>
          </div>
          <div
            class="showBtn"
            style="position: absolute; bottom: 0px; right: 0px"
            v-if="showBtn"
          >
            <el-button type="primary" plain @click="handleReply">{{
              btnContent
            }}</el-button>
          </div>
        </div>
      </div>
      <!--  -->
      <publicReply
        :info="dialogInfo"
        :title="dialogTitle"
        :type="dialogType"
        :show="show"
        @changeShow="changeShow"
      ></publicReply>
    </div>
  </div>
</template>
<script>
import publicReply from "../expertManage/expertManageChildren/publicReply";
export default {
  data() {
    return {
      // 
      dialogTitle: "",
      dialogType: "",
      show: false,
      dialogInfo: {
        replyId: "",
        quesId: "",
        replierId: "",
        replierName: "",
        reply: "",
        images: "",
        experts: true,
      },
      // 
      showBtn: false,
      btnContent: "",
      userExpertId: "",
      userExpertName: "",
      isExpert: false,
    };
  },
  components: {
    publicReply,
  },
  props: ["quesInfo"],
  watch: {
    async quesInfo() {
      console.log("this.quesInfo: ", this.quesInfo);
      if (this.quesInfo.userId) {
        // 
        if (this.$store.state.userData.userId == this.quesInfo.userId) {
          // 
          this.showBtn = true;
          this.btnContent = "";
        } else {
          // 
          this.isExpert = await this.judgeUserIsExpert(
            this.$store.state.userData.userId
          );
          if (this.isExpert) {
            // 
            await this.getExpertInfoByUserId(this.$store.state.userData.userId);
            if (this.userExpertId == this.quesInfo.expertsId) {
              // idid
              this.showBtn = true;
              this.btnContent = "";
            }
          }
        }
      }
    },
  },
  methods: {
    // 
    handleReply() {
      this.changeShow();
      this.dialogType = "add";
      if (this.isExpert) {
        this.dialogTitle = "";
        this.dialogInfo.replierId = this.userExpertId;
        this.dialogInfo.replierName = this.userExpertName;
      } else {
        this.dialogTitle = "";
        this.dialogInfo.replierId = this.quesInfo.userId;
        this.dialogInfo.replierName = this.quesInfo.userName;
      }
      this.dialogInfo.quesId = this.quesInfo.id;
    },
    // 
    changeShow() {
      this.show = !this.show;
    },
    formatTime(date) {
      //date
      const d = new Date(date);
      const month =
        d.getMonth() + 1 < 10 ? "0" + (d.getMonth() + 1) : d.getMonth() + 1;
      const day = d.getDate() < 10 ? "0" + d.getDate() : d.getDate();
      const hours = d.getHours() < 10 ? "0" + d.getHours() : d.getHours();
      const min = d.getMinutes() < 10 ? "0" + d.getMinutes() : d.getMinutes();
      const sec = d.getSeconds() < 10 ? "0" + d.getSeconds() : d.getSeconds();
      const times =
        d.getFullYear() +
        "-" +
        month +
        "-" +
        day +
        " " +
        hours +
        ":" +
        min +
        ":" +
        sec;
      return times;
    },
    // id
    async getExpertInfoByUserId(id) {
      await this.reqM2Service(`/info/experts/findByUser/${id}`, {}, "get").then(
        (res) => {
          res = res.data;
          if (res.code === 20000) {
            res = res.data;
            this.userExpertId = res.id;
            this.userExpertName = res.name;
            console.log("this.userExpertName: ", this.userExpertName);
            // console.log("this.userExpertId: ", this.userExpertId);
          } else {
            this.$message({
              message: "",
            });
          }
          this.loading = false;
        }
      );
    },
    // userId
    async judgeUserIsExpert(id) {
      let flag = false;
      await this.reqM2Service(`/info/experts/isExperts/${id}`, {}, "get").then(
        (res) => {
          res = res.data;
          if (res.code == 20000) {
            // console.log("res: ", res);
            flag = res.flag;
          }
        }
      );
      return flag;
    },
  },
  async created() {},
};
</script>
<style lang="less" scoped>
.res_time {
  margin-top: 8px;
  color: #ccc;
  font-size: 12px;
}
.miniQuestionDetailCard {
  /deep/ .que_item {
    .que_text {
      .ccy-drvider {
        margin: 10px 0 7px 0;
      }
      .up {
        .queicon {
          padding: 4px 4px;
          background-color: rgb(103, 212, 86);
          border-radius: 4px;
          color: #fff;
          font-size: 15px;
          font-weight: 700;
          margin-right: 3px;
        }

        .quetext {
          font-weight: 200;
        }
      }

      .img_content {
        img {
        }
      }
    }

    .info {
      display: flex;
      justify-content: space-between;
      color: #ccc;
      font-size: 12px;
      margin-top: 20px;
      .left {
        img {
          width: 20px;
          height: 20px;
          background-color: greenyellow;
          border-radius: 50%;
        }

        .user_name {
        }
      }

      .update_time {
      }
    }
  }
}
</style>